}

.body-theme {
  color: var(--text-color-main);
  background-color: var(--text-color-header);
}

.body-theme.dark-theme {
  color: var(--text-dark-theme);
  background-color: var(--dark-theme);
}

.theme {
  width: 100px;
  height: 30px;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  margin-right: 32px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 768px) {
  .theme {
    margin-right: 32px;
  }
}

@media screen and (max-width: 420px) {
  .theme {
    margin-top: 80px;
    margin-right: 20px;
  }
}

.theme-text {
  color: var(--bg-color);
  font-size: 10px;
}

.switch {
  width: 30px;
  height: 16px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(-50%);
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 9px;
  width: 9px;
  background-color: var(--bg-color);
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: rgba(0, 0, 0, 0);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

input:checked + .slider:before {
  transform: translateX(14px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.footer.dark-theme {
  color: var(--text-dark-theme);
  background-color: var(--dark-theme);
}

.footer-text.dark-theme, .footer-team-link.dark-theme {
  color: var(--text-dark-theme);
}

.signin.dark-theme, .registration-form.dark-theme {
